import { Typography, Box, Input, Button } from "@mui/material";

import "./App.css"
import styled from "@emotion/styled";
import Command from "./Commands";
import { useEffect, useState } from "react";

const Text = styled(Typography)`
  position: fixed;
  color: white;
  font-family:Times New Roman, Times, serif;
  font-size: 1rem;
  font-weight: 600;
  margin-left: 50rem;
  border:.1rem solid gray;
  background: rgb(227,170,31);
background: linear-gradient(103deg, rgba(227,170,31,0.25139060663205326) 20%, rgba(212,99,0,0.1259987571022727) 93%);
  border-radius:0 0 1rem 1rem;
  box-shadow:.2rem .2rem .2rem black;
  padding: 0 1rem;
  font-family: sans-serif;

`;
const Text2 = styled(Typography)`
font-size:2rem;
font-weight:600;
color:#662d91;
text-transform: capitalize;
margin-left:2rem;
display:flex;
flex-gap:1rem;
  text-shadow: .3rem 1rem 1rem  gray ;
 
  width:20rem;
  
  border-radius:2rem;
  margin-top:1rem;

  `


const Home = () => {
  const months = [
    "Jan", "Feb", "Mar", "Apr", "May", "Jun",
    "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
  ];
  const date = new Date();
const daysOfWeek = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
const todayDay = daysOfWeek[date.getDay()];
const number = date.getDate();
  const [values, setValues] = useState(''); // Initialize with an empty string
  const chnaged = (e) => {
    setValues(e.target.value); // Update the 'values' state
  };
const getItem=()=>{
 
    const storedTasks = localStorage.getItem('tasks');
   if(storedTasks){
    return JSON.parse(storedTasks);
   }
   else{
    return [];
   }
  }


const [item,setItem] =useState(getItem());

  const clicked = () => {
    if (values.trim() !== '') { // Ensure the value is not empty
      setItem([...item,values])

     setValues('');
    }
  };


 

 
  useEffect(()=>{
    localStorage.setItem('tasks',JSON.stringify(item))
    },[item,values])



  return (
    <div className="HomeCoin" >
      <Box>
        <Box>
          <Text >  MY ToDo LIST <h1 style={{color:"Yellow",fontSize:'inherit'}}>{months[new Date().getMonth()]}</h1></Text>
          <Text2> today: {todayDay}   {number}</Text2>
        </Box>
        <div style={{ marginTop: '8rem' }}>
          <Command item ={item} />
        </div>
        <div style={{ display: 'flex', position: 'fixed', bottom: '1rem' ,color:'black'}}>
          <input
            className="taskInput"
            value={values}
            type="text"
            onChange={chnaged}
            style={{color:'black',fontSize:'1rem'}}
            placeholder="input task"
          
           
          />
                    <button className="taskButton" onClick={clicked}>add task</button>

          <div className="butt">
          <Button
            onClick={clicked}
          
           
            style={{
              marginLeft: '47rem',
              background: 'wheat',
              height: '2.5rem',
              width: '10rem',
              boxShadow: '.2rem .2rem .2rem .2rem #97979780',
              fontSize: '1.3rem',
              fontWeight: '700',
              color: 'black',
             
            }}
            
          >
            ADD TASK
          </Button>
          </div>
        </div>
      </Box>
    </div>
  );
};

export default Home;
