import { Checkbox } from "@mui/material";
import ReplayCircleFilledSharpIcon from '@mui/icons-material/ReplayCircleFilledSharp';
import { useEffect, useState } from "react";
import { nanoid } from "nanoid";

const date = new Date();

const Command = ({item}) => {
  const taa = JSON.parse(localStorage.getItem('tasks'));
  const [data, setItem] = useState(item);

  const getItem=()=>{
 
    const storedTasks = localStorage.getItem('todo');
   if(storedTasks){
    return JSON.parse(storedTasks);
   }
   else{
    return [];
   }
  }




    


const [complete,setCompleted] =useState([]);

console.log(data,'taa')
  const time = date.getHours();
  const minutes = date.getMinutes();
  const currentTime = time + ":" + minutes;
  const checked = (index) => {
    // Create a new array without the item at the specified index
   const splice = item.splice(index, 1);
    setCompleted([...complete,splice])
    
   

    localStorage.setItem('tasks', JSON.stringify(item));

  };


  const redo = (index) => {
    const completedTask = complete[index];

    // Create a new array without the completed task
    const updatedComplete = [...complete];
    updatedComplete.splice(index, 1);

    // Update the state with the new data
    setCompleted(updatedComplete);

    // Update the 'data' state with the completed task
    setItem([...data, completedTask]);

    // Update local storage with the new data
    localStorage.setItem('tasks', JSON.stringify([...data, completedTask]));
  }



  return (
    <div style={{ display: 'flex', marginTop: '3rem' }}>
      <div className="commandCoin">
        {item.map((item, index) => (
          <div style={{ display: 'flex' }} key={index}>
            <div className="command">
              <h1 style={{ marginLeft: '1rem', fontSize: '1rem' }}>{item}</h1>
            </div>
            <Checkbox
              style={{ marginBottom: '2rem' }}
              key={nanoid()}
              onClick={() => checked(index)}
              sx={{
                '& .MuiSvgIcon-root': { fontSize: 30, color: 'red' },
                '&.Mui-checked': { color: 'orange[400]' },
              }}
            ></Checkbox>
          </div>
        ))}
      </div>
      <div>
        { complete.map((item, index) => (
          <div style={{ display: 'flex' }} key={index}>
            <div className="command" style={{ backgroundColor: '#41d059',backgroundImage:' linear-gradient(43deg, #41d059 0%, #50c897 46%, #70fff2 100%)'}}>
              <div style={{ display: 'flex', gap: '2rem', alignItems: 'center', justifyContent: 'flex-right' }}>
                <h1><strike>{item}</strike></h1>
                <p>completed</p>
              </div>
            </div>
            <ReplayCircleFilledSharpIcon style={{ height: '3rem', width: '2rem', color: 'wheat' }} onClick={()=>redo(index)}/>
          </div>
        )) }
      </div>
    </div>
  );
}

export default Command;
