import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Front from "./Front";
import Home from "./Home";

const App = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Front />} />
        <Route  path='/home' element={<Home />} />
      </Routes>
    </Router>
  );
};

export default App;
