import { useState } from 'react';
import './App.css'
import { useNavigate } from 'react-router-dom';
import zoro1 from './Images/zoro1.png'
import zoro2 from './Images/zoro2.png'
import {Button, Typography} from '@mui/material';
import styled from '@emotion/styled';
const Front =()=>{
  const navigate = useNavigate()

const [transformed,setTransformed] = useState(false);


const Text = styled(Typography)`

color:white;
font-weight:600;
`

const handleClick =()=>{

  setTimeout(() => {
    navigate('/home'); // Navigate after a 3-second delay
  },1000);
}






  return(
    <div  className='frontCoin'>

<div className={"box" +  (transformed? "transform":"")}>

<img className='frontImage1' src={transformed?zoro2:zoro1} alt='auto' />

    </div>
    <Button className='frontButton'  style={{marginLeft:'35rem',background:'black',height:'4rem',width:'11rem'}}  onClick={()=>{ setTransformed(!transformed);handleClick()}}><Text> COMMAND</Text> </Button>

    </div>
  )
}
export default Front;